.helpOption {
    display: flex;
    flex-direction: column;
    -webkit-box-shadow: 0px 0px 3px 1px rgba(0, 0, 0, 0.17);
    box-shadow: 0px 0px 3px 1px rgba(0, 0, 0, 0.17);
    height: 500px;
    width: 385px;
    background-color: white;
    border-radius: .25em;
    position: relative;
    margin-left: 9px;
    margin-right: 9px;
    margin-top: 1em;
    margin-bottom: 1em;
    align-items: flex-start;
    justify-content: flex-start;
}
.helpOption > img {
    width: 75px;
    height: 75px;
    object-fit: cover;
    cursor: pointer;
    margin: 1em 0 1em 2em;
}
.helpOption > h3 {
    font-size: 1.5rem;
    font-weight: 900;
    text-shadow: .5px 0 black;
    max-width: 70%;
    text-overflow: wrap;
    margin: 0.5em 0 1em 2em;
}
.helpOption__option {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-left: 4em;
    padding-right: 4em;
    max-width: 100%;
}
.helpOption__option > div {
    display: flex;
    align-items: flex-start;
    margin-bottom: 1em;
    cursor: pointer;
}
.helpOption__option > div > a {
    font-size: 1rem;
    font-weight: 600;
}
.helpOption__option > div >span {
    font-size: 1rem;
    font-weight: 600;
    margin-right: 1em;
}
.helpOption__option > div:hover a {
    text-decoration: underline;
}
.helpOption__option > a {
    align-self: center;
    width: 80%;
    position: absolute;
    text-align: center;
    font-size: .8rem;
    bottom: 50px;
    font-weight: 600;
    border-radius: .1em;
    border: 2px solid rgb(200,200,200);
    padding-top: 1.25em;
    padding-bottom: 1.25em;
}
.helpOption__option > a:hover {
    border: 2px solid black;
}
.help__faq {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    width: 100%;
    padding: 0 .5em 0 .5em;
    max-width: 740px;
}
@media screen and (max-width: 450px){
    .helpOption {
        width: 300px;
    }
    .helpOption > img {
        height: 50px;
        width: 50px;
    }
}